.button {
  background: var(--blue);
  border-radius: 4px;
  color: white;
  text-decoration: none;
  height: 56px;
  padding: 0 36px;
  margin: 10px 20px 10px 0;
  display: inline-flex;
  font-size: inherit;
  align-items: center;
  font-weight: bold;

  justify-content: center;
}

  @media (max-width: 520px) {.button {
    font-weight: 500;
    padding: 0 28px;
    height: 42px
}
  }

  .button:focus,
  .button:hover {
    outline: none;
    background: var(--dark-blue);
  }

.center {
  margin: 10px;
}
