.header {
  height: 80vh;
  background-size: cover;
  position: relative;
}

.small {
  height: auto;
}

.small h1,
  .small h2,
  .small h3 {
    background: var(--blue);
    color: white;
  }

.headline {
  display: inline-block;
  background: white;
  padding: 3px 10px 10px;
}

.headlineWrap {
  padding: 1em;
}

.buttonWrap {
  text-align: center;
  position: absolute;
  bottom: 20px;
  left: 10px;
  right: 10px;
  display: block;
}
