/* open-sans-300 - latin */
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 300;
  src: url('../../../static/fonts/open-sans-v16-latin-300.eot'); /* IE9 Compat Modes */
  src: local('Open Sans Light'), local('OpenSans-Light'),
    url('../../../static/fonts/open-sans-v16-latin-300.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('../../../static/fonts/open-sans-v16-latin-300.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('../../../static/fonts/open-sans-v16-latin-300.woff') format('woff'),
    /* Modern Browsers */
      url('../../../static/fonts/open-sans-v16-latin-300.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../../../static/fonts/open-sans-v16-latin-300.svg#OpenSans')
      format('svg'); /* Legacy iOS */
}
/* open-sans-300italic - latin */
@font-face {
  font-family: 'Open Sans';
  font-style: italic;
  font-weight: 300;
  src: url('../../../static/fonts/open-sans-v16-latin-300italic.eot'); /* IE9 Compat Modes */
  src: local('Open Sans Light Italic'), local('OpenSans-LightItalic'),
    url('../../../static/fonts/open-sans-v16-latin-300italic.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */
      url('../../../static/fonts/open-sans-v16-latin-300italic.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('../../../static/fonts/open-sans-v16-latin-300italic.woff')
      format('woff'),
    /* Modern Browsers */
      url('../../../static/fonts/open-sans-v16-latin-300italic.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../../../static/fonts/open-sans-v16-latin-300italic.svg#OpenSans')
      format('svg'); /* Legacy iOS */
}
/* open-sans-regular - latin */
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  src: url('../../../static/fonts/open-sans-v16-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('Open Sans Regular'), local('OpenSans-Regular'),
    url('../../../static/fonts/open-sans-v16-latin-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('../../../static/fonts/open-sans-v16-latin-regular.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('../../../static/fonts/open-sans-v16-latin-regular.woff')
      format('woff'),
    /* Modern Browsers */
      url('../../../static/fonts/open-sans-v16-latin-regular.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../../../static/fonts/open-sans-v16-latin-regular.svg#OpenSans')
      format('svg'); /* Legacy iOS */
}

:root {
  --dark: #555;
  --blue: #2d4a82;
  --dark-blue: #072742;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-family: 'Open Sans';
  background-color: white;
  color: var(--dark);
}

@media (max-width: 520px) {
    body {
      font-size: 16px;
    }
  }
